/* eslint-disable react/no-danger */
import React, { memo, useMemo } from 'react';
import { TypeActionInteraction } from 'containers/Home/types';
import { DefaultIconWrapper } from './styled';

interface Props {
  svg?: string;
  size?: number;
  iconType?: string;
  bgColorDefault: string;
  iconColorDefault: string;
  typeHotspot: TypeActionInteraction;
}

const DefaultIcon = ({
  svg,
  iconType,
  size = 3,
  typeHotspot,
  bgColorDefault,
  iconColorDefault,
}: Props) => {
  const svgSize = 6 * size;
  const shouldRenderSvg = iconType === 'svg' && svg;

  const classIcon = useMemo(() => {
    switch (typeHotspot) {
      case 'point':
        return 'map-marker-alt';
      case 'article':
        return 'newspaper';
      case 'link':
        return 'link';
      case 'sound':
        return 'waveform';
      case 'video':
        return 'video';
      case 'image':
        return 'images';
      case 'product':
        return 'tshirt';
      default:
        return '';
    }
  }, [typeHotspot]);

  return (
    <DefaultIconWrapper bgColor={bgColorDefault} iconColor={iconColorDefault}>
      {shouldRenderSvg ? (
        <div
          style={{ width: `${svgSize}px`, height: `${svgSize}px` }}
          dangerouslySetInnerHTML={{ __html: svg }}
        />
      ) : (
        <i aria-hidden className={`fas fa-${classIcon}`} />
      )}
    </DefaultIconWrapper>
  );
};

export default memo(DefaultIcon);
